Vue.use(VueRouter)

const routes = [{
    path: '/login',
    component: require('./login')
}, {
    path: '/',
    redirect: '/login'
}]

const router = new VueRouter({
    routes
})


const app = new Vue({
    el: '#app',
    router: router,
    components: {
        App: require('./app')
    },
    template: '<app></app>'
})

window.apiready = function() {
    api.addEventListener({
        name: 'keyback'
    }, function(ret, err) {
        var hashs = ['#/login']
        if (hashs.indexOf(window.location.hash) > -1) {
            api.closeWidget()
        } else {
            history.back()
        }
    })
}


